<template>
  <div>
    <div class="nav-title-box" @click="changeTitle">
      <span class="text" :class="{updatetext: isUpdate} ">{{navTitle}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "NavTitle",
  props: {
    navTitle: String
  },
  data() {
      return {
          isUpdate: false
      }
    
  },
  methods: {
    changeTitle() {
      this.$store.commit("changeCategory", this.navTitle);
      this.$store.commit("changeHidden");
      let NewPage = "_empty" + "?time=" + new Date().getTime() / 500;
      this.$router.push(NewPage);
      this.$router.go(-1);
    }
  },
  beforeMount() {
      if(this.navTitle === this.$store.state.nowCategory){
          this.isUpdate = true;
      }
  }
};
</script>

<style lang="less">
.nav-title-box {
  font-size: 1.5vw;
  margin-top: 0.5vw;
  padding-bottom: 0.5vw;
  .text {
    padding-bottom: 4px;
  }
  .text:hover {
    border-bottom: 3px red solid;
  }
  .updatetext {
    padding-bottom: 4px;
    border-bottom: 3px red solid;
  }
}

@media (max-width: 800px) {
  .nav-title-box{
    font-size: 4.5vw;
    font-weight: 540;
  }
}
</style>