<template>
  <div class="nav-container">
    <article-preview v-for="(item, index) in msg" :key="index" :msg="item"></article-preview>
  </div>
</template>

<script>
import ArticlePreview from "../components/ArticlePreview.vue";

export default {
  name: "ArticleSide",
  data() {
    return {
      msg: [],
      nowCategory: this.$store.state.nowCategory
    }
  },
  components: {
    ArticlePreview
  },
  methods: {
    getArticle() {
      for(let i of this.$store.state.article) {
        if(this.$store.state.nowCategory === i.category) {
          this.msg = i.items
        }
      }
    }
  },
  beforeMount() {
    this.getArticle()
  },
  watch:{
    nowCategory(newValue){
    }
  }
};
</script>

<style lang="less">
.nav-container {
  text-align: center;
}
</style>