<template>
  <div class="art-box">
    <div class="art" :class={ishidden:isHidden} @click="toArticle">
      <div class="cover"></div>
      <div class="title">
        <div>{{msg.title}}</div>
      </div>
    </div>
    <div class="date">2020.10.15</div>
  </div>
</template>

<script>
export default {
  name: "ArticlePreview",
  props: ["msg"],
  data() {
    return {};
  },
  methods: {
    toArticle() {
      this.$store.commit("changeArticle", this.msg.id);
      this.$router.push("myarticle");
    }
  },
  computed:{
    isHidden() {
      return !this.$store.state.isHidden
    }
  },
  watch:{
    isHidden(x,y){
      return
    }
  }
};
</script>

<style lang="less">
.art-box {
  width: 90%;
  margin-bottom: 5vw;
  .art {
    border-radius: 10px;
    // background-color: green;
    background-image: url(../../public/1.jpg);
    width: 100%;
    height: 30vw;
    display: flex;
    justify-content: center;
    align-items: center;
    .title {
      font-size: 5vw;
    }
  }
  @media screen and (max-width: 800px) {
    .art {
      width: 100%;
      position: relative;
      
    }
    .ishidden{
      filter: blur(2px);
    }
  }

  .art:hover {
    opacity: 0.8;
  }
  .date {
    text-align: right;
    margin-top: 0.5vw;
    margin-right: 1vw;
    font-size: 1.5vw;
  }
}
</style>