<template>
  <div class="article-box">
      <div class="fixed" :class={ishidden:isHidden}>
          <nav-title v-for="(item, index) in titles" :key="index" :navTitle="item"></nav-title>
      </div>
      
  </div>
</template>

<script>
import NavTitle from '../components/NavTitle.vue'

export default {
    name: 'ArticleNav',
    data() {
        return {
            titles: [],
        }
    },
    components: {
        NavTitle
    },
    methods: {
        getArticleTitle() {
            for (let i of this.$store.state.article) {
                this.titles.push(i.category)
            }
        }
    },
    computed:{
        isHidden(){
            return this.$store.state.isHidden
        }
    },
    watch:{
        isHidden(newVaule, oldValue){
            return 
        }
    },
    beforeMount() {
        this.getArticleTitle()
    }
}
</script>

<style lang="less">
.article-box{
    .fixed{
        position: fixed;
        left: 6vw;
    }
    @media screen and (max-width: 800px) {
        .fixed{
            position: fixed;
            z-index: 999;
            width: 20%;
            top: 16vw;
            left: 55vw;
        }
        .ishidden{
            display: none;
        }
    }
}
</style>